@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);

*:focus{
    outline: none;
    box-shadow: none;
}

*{
    padding: 0;
    margin: 0;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    box-sizing: border-box;
}

:root{
    font-size: 62.5%;
    scroll-behavior: smooth;
}

body{
    font-family: 'Roboto';
}

::-webkit-scrollbar {
    display: none;
}
  


.content{
    display: flex;
    flex-direction: column;
}

.section-content{
    height: 100vh;
    margin-left: 10rem;
    background: #D8DBE2;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.watermark {
    position: fixed;
    bottom: .5rem;
    right: .5rem;
    color: #1B1B1E;
    font-weight: 400;
    font-size: 1.7rem
}
.nav-ul{
    list-style: none;
    margin-top: 25vh;
}

.nav-link{
    margin-bottom: 3.1rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav-link:first-child{
    margin-top: 3.1rem
}

.nav-img {
    width: 6.9rem;
    height: 4.9rem;
    fill: #D8DBE2;
}

.nav-img:hover{
    fill: #F3F4F6;
}

.nav-link:hover > .nav-img{
    fill: #F3F4F6;
    cursor: pointer;
}

.nav-label:hover{
    cursor: pointer;    
}

.nav-label{
    background: #1B1B1E;
    color: #F3F4F6;
    font-size: 1.8rem;
    padding: .25rem .9rem;
    border-radius: .35rem;
}

.nav-logo{
    position: absolute;
    width: 7.9rem;
    height: 7.9rem;
    border-radius: 50%;
    border: .2rem solid #D8DBE2;
    left: 50%;
    top: 1.6rem;
    transform: translate(-50%, -10%)
}

.nav-logo + h1{
    display: none;
}

.nav-bar{
    display: flex;
    position: fixed;
    justify-content: center;
    background: #1B1B1E;
    width: 10rem;
    height: 100vh;
    top: 0;
    left: 0;
}
.nav-img + span{
    opacity: 0;
    transition: none .1s ease-out;
    transition-property: opacity, left, transform;
    transition-delay: .1s;
    position: fixed;
    left: 2rem;
}

.nav-link:hover > span {
    opacity: 10;
    left: 10.2rem;
    transform: translateY(-55%)
}




.menu{
    display: flex;
    flex-direction: row;
}
.menu-holder{
    flex: 1 0;
    margin-left: 10rem;
    width: 100px;
    height: 100vh;
    background: #A9BCD0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.name-div{
    display: flex;
    flex-direction: column;
}

.name-heading{
    font-size: 6.4rem;
    color: #1B1B1E;
}

.icon-holder{
    display: flex;
    justify-content: center;
}

.socials-logo-anchor{
    margin: .1rem 1.8rem;
}

.socials-logo{    
    width: 4.9rem;
    height: 4.9rem;
    fill: #1B1B1E;
}

.socials-logo:hover{
    fill: black;
}

.arrow-down{
    position: absolute;
    bottom: 1rem;
}

.arrow-down-icon{
    fill:  #1B1B1E;
    width: 2.4rem;
}

.arrow-down-icon:hover{
    fill: black;
    cursor: pointer;
}
.card{
    background: #F3F4F6;
    border: solid #1B1B1E .2rem;
    border-radius: 3rem;
    box-shadow: 0 .1rem .4rem 0 #1B1B1E;
}
.about-me-holder{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about-me-heading{
    font-size: 4.8rem;
}

.card-about-me{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 2rem;
}

.about-me-paragraph{
    font-size: 3rem;
    font-weight: 300;
    margin-left: 2rem;
}

.about-me-button-holder{
    display: flex;
    align-items: space-evenly;
}

.about-me-button{
    width: 25.6rem;
    height: 8rem;
    margin-bottom: 5rem;
    margin:  0 2rem;
    border-radius: 5rem;
    border: 0;
    background: #1B1B1E;
    font-size: 2.8rem;
    color: #D8DBE2;
}

.about-me-button:hover{
    background: black;
    color: #F3F4F6;
    cursor: pointer;
}
.services{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.services-heading{
    font-size: 4.8rem;
}

.service-card-holder{
    display: flex;
    flex-wrap: wrap;
}

.card-service{  
    margin: 0 2rem; 
}

.service-text{
    margin: 1.5rem;
    font-size: 2.7rem;
    text-align: center;
    font-weight: 300;
}

.service-text b{
    font-weight: 400;
}

.services-footer{
    font-size: 2.4rem;
    font-weight: 300;
}

.services-footer a {
    text-decoration: none;
    color: #FF003D;
}

.services-footer a:hover {
    cursor: pointer;
}

.projects-holder{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.projects-heading{
    font-size: 4.8rem;
}

.projects-ul{
    list-style: none;
    display: flex;
    font-size: 1.8rem;
    margin-top: 5rem;
}

.projects-li{
    margin: 0 .5rem;
}

.projects-card-holder{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 108.8rem;
    margin: 4rem 0;
}

.projects-footer{
    font-size: 2.4rem;
    font-weight: 300;
}

.projects-footer a {
    text-decoration: none;
    color: #FF003D;
}
.project{
    background: #F3F4F6;
    border: solid #1B1B1E .2rem;
    border-radius: 3rem;
    box-shadow: 0 .1rem .4rem 0 #1B1B1E;
    width: 25.6rem;
    height: 25.6rem;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: 50%;
    margin: .8rem;
    
}


.contact-me{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.contact-heading{
    font-size: 4.8rem;
}

.form-contact{
    display: flex;
    flex-direction: column;
    align-items: center
}

.fl-div{
    margin-bottom: 2.1rem;
}

.fl-input{
    margin: 0 1rem;
}

.form-input{
    width: 58.7rem;
    height: 7.6rem;
    font-size: 2.4rem;
    border: solid #1B1B1E .2rem;
    border-radius: 3rem;
    /* margin: 2.1rem 1rem; */
    padding: 2rem
}

.fsubject{
    width: 119.4rem;
    margin: 2.1rem 1rem;
}

.fmessage{
    margin: .8rem 1rem 2.1rem 1rem;
    width: 119.4rem;
    height: 36.6rem;
    font-size: 2.4rem;
    font-family: roboto; 
    resize: vertical;
}

.fbutton-holder{
    align-self: center;
}

.submit-button{
    width: 25.6rem;
    height: 8rem;
    border-radius: 5rem;
    border: 0;
    background: #1B1B1E;
    font-size: 2.8rem;
    color: #D8DBE2;
}

.submit-button:hover{
    background: black;
    color: #F3F4F6;
    cursor: pointer;
}


