.nav-img + span{
    opacity: 0;
    transition: none .1s ease-out;
    transition-property: opacity, left, transform;
    transition-delay: .1s;
    position: fixed;
    left: 2rem;
}

.nav-link:hover > span {
    opacity: 10;
    left: 10.2rem;
    transform: translateY(-55%)
}



